import React, { Component, Fragment } from 'react';
import Icon from '@mdi/react';
import { Row, Col, Container } from 'reactstrap';
import { HashLink } from 'react-router-hash-link';
import { mdiLinkedin, mdiHandshake  as contactIco, mdiTwitter } from '@mdi/js';
import { local_urls } from './../layout/default/urls';


export default class AboutMe extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    render() {
        return (
            <Fragment>
                <Container>
                    <section>
                        <h2>
                            About me
                    </h2>
                        <p>
                            Hi, my name is Radovan Pranda and currently I am studying the last year of master degree
                        in Faculty of electrical engineering and computer science at VÅ B Technical University of Ostrava.<br />
                        During my studies, I worked on several interesting projects. Some of these projects are mentioned <HashLink to="/#projects">here</HashLink>.
                        Most of them were constructed as B2B solution. Because of this reason they are marked as closed source software. I had
                        opportunity to collaborate with many companies, gain experiences in various fields of IT, find new friends and realize many of my ideas.
                    </p>
                    </section>
                </Container>
                <div className="banner linked-in">
                    <Container>
                        <Row>
                            <Col>
                                <Icon path={mdiLinkedin} size={7.0} color="#fff" />
                            </Col>
                            <Col>
                                <h2>
                                    Career
                                </h2>
                                <p>
                                    Do you want to know more about my career?
                                </p>
                                <a className="btn btn-white" href={local_urls["linkedin"]}>See my Linkedin profile</a>
                            </Col>
                        </Row>
                    </Container>
                </div>
                <Container>
                    <section>
                        <h2>
                            Hobbies
                        </h2>
                        <p>
                            If I had to name all my hobbies, it would take long long time. So I decided to choose only these few:
                            <ul>
                                <li>learning new things</li>
                                <li>coding (programming)</li>
                                <li>data analysis</li>
                                <li>graphic and design</li>
                                <li>rollerblading / skating</li>
                                <li>cycling (downhill, freeride, ...)</li>
                                <li>swimming</li>
                                <li>drawing</li>
                                <li>discovering nature and monuments</li>
                                <li>...</li>
                            </ul>
                        </p>
                    </section>
                </Container>
                <div className="banner contact" id="contact">
                    <Container>
                        <Row>
                            <Col>
                                <Icon path={contactIco} size={7.0} color="#fff" />
                            </Col>
                            <Col>
                                <h2>
                                    Contact
                                </h2>
                                <p>
                                    <b>E-mail:</b> pranda.radovan.b@gmail.com<br />
                                    <div className="pt-1">
                                        <a href={local_urls["linkedin"]} className="btn-ico-link-white">
                                            <Icon path={mdiLinkedin} size={1.1} />
                                        </a>
                                        <a href={local_urls["twitter"]} className="btn-ico-link-white">
                                            <Icon path={mdiTwitter} size={1.1} />
                                        </a>
                                    </div>
                                </p>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </Fragment>
        );

    }
}